.checkerboard {
  --unit-w: 30px;
  --mk-w: 5px;
  --line-w: 1px;
  --point-w: 20px;
  width: calc(var(--unit-w) * 16);
  height: calc(var(--unit-w) * 16);
  background: #e6a23c;
  position: relative;
}

.checkerboard .checkerboard-ck {
  cursor: pointer;
  width: calc(var(--unit-w) * 16);
  height: calc(var(--unit-w) * 16);
  position: absolute;
  top: 0;
  left: 0;
}

.checkerboard .checkerboard-win {
  width: calc(var(--unit-w) * 16);
  height: calc(var(--unit-w) * 16);
  color: #fff;
  justify-content: center;
  align-items: center;
  font-size: 28px;
  font-weight: 600;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.checkerboard .h-line {
  height: var(--line-w);
  width: calc(var(--unit-w) * 14);
  left: var(--unit-w);
  background: #000;
  position: absolute;
}

.checkerboard .v-line {
  width: var(--line-w);
  height: calc(var(--unit-w) * 14);
  top: var(--unit-w);
  background: #000;
  position: absolute;
}

.checkerboard .mk {
  height: var(--mk-w);
  width: var(--mk-w);
  background: #000;
  border-radius: 50%;
  position: absolute;
}

.checkerboard .p-mk {
  height: var(--mk-w);
  width: var(--mk-w);
  background: red;
  border-radius: 50%;
  position: absolute;
}

.checkerboard .point {
  height: var(--point-w);
  width: var(--point-w);
  border-radius: 50%;
  display: none;
  position: absolute;
}

.checkerboard .point[color="1"] {
  background: #000;
  display: block;
}

.checkerboard .point[color="0"] {
  background: #fff;
  display: block;
}

